import { ColorOrganizerHeader } from './header/color-organizer-header.js'
import { ColorList } from './color/colorList.js'

import './colorOrganizer.scss'

export default class ColorOrganizer extends React.Component {

  constructor (props) {
    super(props)
    this.state = {
      colors: [
        { 
          color: { backgroundColor: '#004080' },
          title: "favorite blue",
          starSelected: 3,
          id: "2345"
        },
        { 
          color: { backgroundColor: '#eee' },
          title: "geeky grey",
          starSelected: 5,
          id: "2347"
        },
        { 
          color: { backgroundColor: '#0096ce' },
          title: "great blue",
          starSelected: 5,
          id: "2348"
        }
      ],
      starLimit: 5
    }
    this.onRate = this.onRate.bind(this)
    this.onRemove = this.onRemove.bind(this)
  }

  onRate (num, id) {
    const { colors } = this.state
    const newColors = colors.map((color) => {
      if (color.id === id) {
        color.starSelected = num
      }
    })
    console.log(newColors)
    this.setState({
      colors: newColors
    })
  }

  onRemove (id) {
    const msg = `you tried to remove but you cant!! id: ${id}`
    window.alert(msg)
  }

  render () {
    const { onRate, onRemove } = this
    const { colors, starLimit } = this.state

    return (
      <div className="color-organizer-container">
        <ColorOrganizerHeader />
        <ColorList colors={colors}
          starLimit={starLimit}
          onRate={onRate}
          onRemove={onRemove} />
      </div>
    )
  }
}



